:root {
  --font-family: "Open Sans", sans-serif;
  --font-family2: "Exo", sans-serif;
  --body-bg: var(--secondary-color1);
  --color-body: #585858;
  --base-transition: .3s;
  --container-offset: 15px;
  --burger-width: 25px;
  --burger-segment-hight: 3px;
  --burger-segment-radius: 5px 0 0 5px;
  --burger-segment-gap: 8px;
  --primary-color1: #7F3A6A;
  --primary-color2: #E9A923;
  --secondary-color1: #FDFCFC;
  --secondary-color2: #FFF;
  --secondary-color3: #313131;
  --secondary-color4: #F9F1EE;
}

h1,
.-h1 {
  font-weight: 700;
  font-family: var(--font-family2);
  line-height: 140%;
  font-size: clamp(28px, 3.1vw, 44px);
  color: var(--secondary-color3);
}

.-h2 {
  font-weight: 700;
  font-size: clamp(24px, 3.1vw, 32px);
  line-height: 140%;
  color: var(--secondary-color3);
  width: fit-content;
}

.-h3 {
  font-weight: 700;
  font-size: clamp(18px, 3.1vw, 24px);
  color: var(--secondary-color3);
}

.-h4 {
  font-weight: 600;
  font-size: clamp(18px, 2.813vw, 20px);
}

.accent {
  text-transform: capitalize;
  color: rgba(var(--cl-d), 1);
}

[data-btn-modal] svg {
  pointer-events: none;
}

.modal {
  display: none;
  margin: auto;
  transform: scale(0.8);
}
.modal.active {
  display: flex;
  transform: scale(1);
}
.modal__box .close {
  position: absolute;
  right: 15px;
  top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(var(--cl-d), 1);
}
.modal__box .close svg {
  width: clamp(15px, 3.26vw, 20px);
  height: auto;
  pointer-events: none;
}

