@media (max-width: 1112px) {
  .site-header .nav-main__links .menu-link {
    font-size: 18px;
  }
}
@media (max-width: 1112px) {
  .site-header .nav-main .c-btn {
    opacity: 0;
    transform: translateY(0px);
    transition: all 0.5s ease;
  }
}
.site-header__col.-left {
  position: relative;
  z-index: 1;
}

.nav-main__btn {
  display: none;
  justify-content: center;
  align-items: center;
  height: 30px;
  border-radius: 3px;
  border: 0 none;
  color: var(--burger-color);
  background-color: var(--burger-bg);
  padding: 0;
  cursor: pointer;
  text-align: center;
  transition: all var(--base-transition);
  z-index: 10;
}
@media (max-width: 1112px) {
  .nav-main__btn {
    display: flex;
  }
}
.nav-main__btn .burger-icon {
  width: var(--burger-width);
  height: 20px;
  cursor: pointer;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-main__btn .burger-icon > span {
  position: relative;
  width: 100%;
  height: var(--burger-segment-hight);
  background: var(--primary-color1);
  transition: all 0.4s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--burger-segment-radius);
}
.nav-main__btn .burger-icon > span::after, .nav-main__btn .burger-icon > span::before {
  content: "";
  position: absolute;
  z-index: 1;
  left: 0;
  width: 100%;
  height: var(--burger-segment-hight);
  border-radius: var(--burger-segment-radius);
  background: inherit;
  transition: all 0.4s ease;
}
.nav-main__btn .burger-icon > span:after {
  top: var(--burger-segment-gap);
}
.nav-main__btn .burger-icon > span::before {
  top: calc(var(--burger-segment-gap) * -1);
}

@media (max-width: 1112px) {
  .nav-main__wrap .nav-main__links .menu-item {
    opacity: 0;
    transition: all 0.5s ease;
    padding: 6px 0;
  }
  .nav-active {
    overflow: hidden;
  }
  .nav-active .nav-main__btn .burger-icon {
    transform: rotate(45deg);
  }
  .nav-active .nav-main__btn .burger-icon > span:after {
    opacity: 0;
    transition: all var(--base-transition);
  }
  .nav-active .nav-main__btn .burger-icon > span::before {
    transform: rotate(-90deg);
    top: 0;
    transition: all var(--base-transition);
  }
  .nav-active .nav-main__wrap .nav-main {
    overflow: auto;
    transform: translateY(-80px);
    transition: transform 1.2s ease-in-out;
  }
  .nav-active .nav-main__wrap .menu-item:nth-of-type(1) {
    opacity: 1;
    transform: translateY(10px);
    transition-delay: 1.4s;
  }
  .nav-active .nav-main__wrap .menu-item:nth-of-type(2) {
    opacity: 1;
    transform: translateY(10px);
    transition-delay: 1.5s;
  }
  .nav-active .nav-main__wrap .menu-item:nth-of-type(3) {
    opacity: 1;
    transform: translateY(10px);
    transition-delay: 1.6s;
  }
  .nav-active .nav-main__wrap .menu-item:nth-of-type(4) {
    opacity: 1;
    transform: translateY(10px);
    transition-delay: 1.7s;
  }
  .nav-active .nav-main__wrap .menu-item:nth-of-type(5) {
    opacity: 1;
    transform: translateY(10px);
    transition-delay: 1.8s;
  }
  .nav-active .nav-main__wrap .menu-item:nth-of-type(6) {
    opacity: 1;
    transform: translateY(10px);
    transition-delay: 1.9s;
  }
  .nav-active .nav-main__wrap .c-btn {
    opacity: 1;
    transform: translateY(10px);
    transition-delay: 1.8s;
  }
}
.site-footer {
  padding: 65px 15px;
  background: var(--secondary-color4);
  text-align: center;
}
.site-footer__list {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 25px 0;
}
@media (max-width: 767px) {
  .site-footer__list {
    flex-direction: column;
  }
}
.site-footer__list .menu-link {
  padding: 12px 15px;
}
.site-footer strong {
  display: block;
  color: var(--secondary-color3);
}
.site-footer__socials {
  gap: 25px;
}
@media (max-width: 767px) {
  .site-footer__socials {
    flex-direction: row;
  }
}
.site-footer__socials a {
  color: var(--primary-color1);
}
@media (hover: hover) {
  .site-footer__socials a:hover {
    color: var(--primary-color2);
  }
}
@media (hover: none) {
  .site-footer__socials a:active {
    color: var(--primary-color2);
  }
}